.Header {
  width: 100%;
  background-color: #c7e9eb;
  display: grid;
  grid-template-areas: "titleBox buttons";
  grid-template-columns: 30vw 1fr;
  height: 8vh; }
  .Header .title-box {
    text-decoration: none;
    align-self: center;
    grid-area: titleBox;
    display: grid;
    grid-template-areas: "logo title";
    grid-template-columns: 8vw 1fr; }
  .Header .logo {
    width: 5vw;
    height: 8vh;
    margin-left: 3vw;
